/* Основные стили контейнера */
.spirit-pets-container {
  padding: 1rem;
  max-width: 1200px;
  margin: 0 auto;
}

.pets-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}

.tame-pet-button {
  padding: 0.6rem 1.2rem;
  background-color: #8e44ad;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  transition: background-color 0.3s;
}

.tame-pet-button:hover {
  background-color: #732d91;
}

/* Стили для сетки питомцев */
.pets-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 1.5rem;
  margin-top: 1.5rem;
}

.no-pets-message {
  padding: 2rem;
  text-align: center;
  background-color: rgba(240, 240, 240, 0.3);
  border-radius: 8px;
  margin-top: 2rem;
}

/* Стили для карточки питомца */
.pet-card {
  border: 2px solid #ccc;
  border-radius: 8px;
  overflow: hidden;
  background-color: rgba(0, 0, 0, 0.2);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s, box-shadow 0.3s;
}

.pet-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
}

.pet-card-active {
  box-shadow: 0 0 0 2px gold, 0 4px 8px rgba(0, 0, 0, 0.2);
}

.pet-card-header {
  padding: 1rem;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.pet-name {
  margin: 0;
  font-size: 1.3rem;
}

.pet-type {
  font-size: 0.9rem;
  opacity: 0.9;
}

.pet-active-badge {
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  background-color: gold;
  color: black;
  padding: 0.2rem 0.5rem;
  border-radius: 12px;
  font-size: 0.7rem;
  font-weight: bold;
}

.pet-card-body {
  padding: 1rem;
}

.pet-info-row {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.8rem;
}

.pet-level, .pet-evolution {
  font-size: 0.9rem;
  color: #ddd;
}

/* Стили для полосы опыта */
.pet-exp-bar-container {
  height: 10px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 5px;
  margin-bottom: 1rem;
  position: relative;
  overflow: hidden;
}

.pet-exp-bar {
  height: 100%;
  background-color: #3498db;
  border-radius: 5px;
  transition: width 0.3s ease;
}

.pet-exp-text {
  font-size: 0.8rem;
  color: #bbb;
  text-align: center;
  margin-top: 0.3rem;
  margin-bottom: 1rem;
}

/* Стили для характеристик */
.pet-stats-container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.pet-stat {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.3rem 0.5rem;
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 4px;
}

.stat-name {
  font-size: 0.9rem;
  color: #ddd;
}

.stat-value {
  font-weight: bold;
  color: white;
}

/* Стили для индикаторов */
.pet-indicators {
  margin-bottom: 1rem;
}

.pet-indicator {
  margin-bottom: 0.5rem;
}

.indicator-name {
  font-size: 0.9rem;
  color: #ddd;
  display: block;
  margin-bottom: 0.2rem;
}

.indicator-bar-container {
  height: 8px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 4px;
  overflow: hidden;
  margin-bottom: 0.2rem;
}

.indicator-bar {
  height: 100%;
  border-radius: 4px;
  transition: width 0.3s ease;
}

.indicator-value {
  font-size: 0.8rem;
  color: #bbb;
  text-align: right;
  display: block;
}

/* Стили для кнопок на карточке */
.pet-card-buttons {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  margin-top: 1rem;
}

.pet-action-button {
  flex: 1;
  min-width: 120px;
  padding: 0.5rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 0.9rem;
  transition: background-color 0.3s;
  text-align: center;
}

.activate-button {
  background-color: #3498db;
  color: white;
}

.activate-button:hover {
  background-color: #2980b9;
}

.feed-button {
  background-color: #2ecc71;
  color: white;
}

.feed-button:hover {
  background-color: #27ae60;
}

.train-button {
  background-color: #e74c3c;
  color: white;
}

.train-button:hover {
  background-color: #c0392b;
}

.details-button {
  background-color: #7f8c8d;
  color: white;
}

.details-button:hover {
  background-color: #636e72;
}

/* Стили для модальных окон */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: #2c3e50;
  border-radius: 8px;
  max-width: 90%;
  width: 500px;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
  color: #ecf0f1;
}

.pet-details-modal {
  width: 700px;
}

.modal-header {
  padding: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.modal-header h3 {
  margin: 0;
}

.modal-close {
  background: none;
  border: none;
  font-size: 1.5rem;
  color: #ecf0f1;
  cursor: pointer;
}

.modal-body {
  padding: 1.5rem;
}

/* Стили для тренировочного модального окна */
.training-info {
  color: #bbb;
  font-size: 0.9rem;
  margin-bottom: 1.5rem;
}

.training-buttons {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  gap: 0.8rem;
  margin-bottom: 1.5rem;
}

.training-button {
  padding: 0.8rem;
  border: none;
  border-radius: 4px;
  background-color: #3498db;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s;
}

.training-button:hover:not(:disabled) {
  background-color: #2980b9;
}

.training-button:disabled {
  background-color: #7f8c8d;
  opacity: 0.7;
  cursor: not-allowed;
}

.training-warning {
  color: #e74c3c;
  padding: 0.8rem;
  background-color: rgba(231, 76, 60, 0.1);
  border-radius: 4px;
  margin-top: 1rem;
}

/* Стили для модального окна деталей */
.pet-details-section {
  margin-bottom: 2rem;
}

.pet-details-section h4 {
  margin-top: 0;
  margin-bottom: 1rem;
  padding-bottom: 0.5rem;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}

.pet-description {
  margin-bottom: 1.5rem;
  color: #ddd;
  line-height: 1.5;
}

.pet-details-row {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem;
  margin-bottom: 1rem;
}

.pet-detail {
  display: flex;
  flex-direction: column;
  flex: 1;
  min-width: 120px;
}

.detail-label {
  font-size: 0.8rem;
  color: #bbb;
  margin-bottom: 0.3rem;
}

.detail-value {
  font-weight: bold;
}

.pet-evolution-description {
  color: #ddd;
  font-style: italic;
  margin-top: 0.5rem;
}

.pet-details-stats {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.8rem;
}

.pet-detail-stat {
  padding: 0.5rem;
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 4px;
  display: flex;
  justify-content: space-between;
}

.detail-stat-name {
  color: #bbb;
}

.detail-stat-value {
  font-weight: bold;
}

.pet-details-combat {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.8rem;
}

.pet-combat-bonus {
  padding: 0.5rem;
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 4px;
  display: flex;
  justify-content: space-between;
}

.combat-bonus-name {
  color: #bbb;
}

.combat-bonus-value {
  color: #2ecc71;
  font-weight: bold;
}

.pet-abilities-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.pet-ability {
  padding: 1rem;
  background-color: rgba(255, 255, 255, 0.05);
  border-radius: 4px;
}

.ability-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}

.ability-name {
  font-weight: bold;
}

.ability-type {
  font-size: 0.8rem;
  color: #bbb;
  background-color: rgba(255, 255, 255, 0.1);
  padding: 0.2rem 0.5rem;
  border-radius: 12px;
}

.ability-description {
  margin-bottom: 0.5rem;
  color: #ddd;
  line-height: 1.4;
}

.ability-cooldown {
  font-size: 0.8rem;
  color: #3498db;
}

.no-abilities {
  color: #bbb;
  text-align: center;
  padding: 1rem;
  background-color: rgba(255, 255, 255, 0.02);
  border-radius: 4px;
}

.pet-details-care {
  margin-top: 1rem;
}

.pet-care-info {
  margin-bottom: 1rem;
}

.care-info-name {
  font-size: 0.9rem;
  color: #ddd;
  display: block;
  margin-bottom: 0.3rem;
}

.care-info-bar-container {
  height: 10px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 0.3rem;
}

.care-info-bar {
  height: 100%;
  border-radius: 5px;
  transition: width 0.3s ease;
}

.care-info-value {
  font-size: 0.8rem;
  color: #bbb;
  text-align: right;
  display: block;
}

.pet-care-timestamps {
  margin-top: 1.5rem;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.pet-timestamp {
  display: flex;
  justify-content: space-between;
  padding: 0.5rem;
  background-color: rgba(255, 255, 255, 0.02);
  border-radius: 4px;
}

.timestamp-label {
  color: #bbb;
}

.timestamp-value {
  color: #ddd;
}

/* Стили для вкладки питомцев - Состояния загрузки и ошибки */
.pets-loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  height: 300px;
}

.pets-loading-spinner {
  width: 40px;
  height: 40px;
  margin-bottom: 1rem;
  border: 4px solid rgba(255, 165, 0, 0.3);
  border-top: 4px solid #ff9900;
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.pets-error-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  height: 300px;
  text-align: center;
}

.pets-error-message {
  color: #ff4d4d;
  margin-bottom: 1rem;
  font-size: 1.1rem;
}

.pets-refresh-button {
  padding: 0.5rem 1rem;
  background-color: #4d91ff;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.pets-refresh-button:hover {
  background-color: #3a7dd9;
}

/* Анимация для проблемных компонентов */
@keyframes fadeInOut {
  0% { opacity: 0.6; }
  50% { opacity: 1; }
  100% { opacity: 0.6; }
}

.pets-loading-debug {
  animation: fadeInOut 2s infinite;
  color: #ff9900;
  padding: 5px;
  font-size: 0.8rem;
  margin-top: 10px;
}

/* Адаптивность для мобильных устройств */
@media (max-width: 768px) {
  .pets-grid {
    grid-template-columns: 1fr;
  }
  
  .pet-details-stats,
  .pet-details-combat {
    grid-template-columns: 1fr;
  }
  
  .pet-details-modal {
    width: 95%;
  }
  
  .training-buttons {
    grid-template-columns: 1fr 1fr;
  }
}
